@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
*{
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  
}

.navbar{
  color: white;
  background-color: rgb(12, 12, 12);
  height: 15vh;
  display: flex;
  align-items: center;
  padding-left: 50px;
  font-family: 'Poppins', sans-serif;
  
}
.body{
  background-color: rgb(0, 0, 0);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}


.footer{
  background-color: rgb(36, 36, 36);
  height: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
  width: 100%;
}
.container{
  display: grid;
  width: 100%;
  justify-content: center;
  background-color: rgb(0, 0, 0);
  height: 65vh;
  align-items: center;
  
}
.timer{
  background-color: rgb(0, 0, 0);
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: white;
  
}
.button{
  display: flex;
  justify-content: space-evenly;
  background-color: rgb(0, 0, 0);
  
}
button{
width: 150px;
height: 50px;
border: none;
font-family: 'Poppins', sans-serif;
background-color: white;
color: rgb(0, 0, 0);
border-radius: 20px;
font-weight: bolder;


 

}
button:hover{
  width: 155px;
  height: 50px;
  
}
.count{
  font-size: 200px;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
}


@media (max-width:900px){
  .navbar{
    color: white;
    background-color: rgb(12, 12, 12);
    height: 15vh;
    display: flex;
    align-items: center;
    padding-left: 20px;
    font-size: 0.6rem;
    font-family: 'Poppins', sans-serif;
  }
  .body{
    background-color: rgb(0, 0, 0);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 100vw;
  }
  
  
  .footer{
    background-color: rgb(36, 36, 36);
    height: 20vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    position: relative;
  }
  .container{
    display: grid;
    
    justify-content: center;
    background-color: rgb(0, 0, 0);
    height: 50vh;
    align-items: center;
    overflow: hidden;
    
  }
  .timer{
    background-color: rgb(0, 0, 0);
    height: 40vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    position: sticky;
  
  }
  .button{
    display: flex;
    justify-content: center;
    width: 70vw;
    background-color: rgb(0, 0, 0);
    justify-content: space-evenly;
    
  }
  button{
  width: 75px;
  height: 25px;
  border: none;
  font-family: 'Poppins', sans-serif;
  background-color: white;
  color: rgb(0, 0, 0);
  border-radius: 20px;
  font-weight: bolder;
  
  
   
  
  }
  
  .count{
    font-size: 100px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
  }
}

